import Vue from 'vue'
import Vuex from 'vuex'

import axios from 'axios'

Vue.use(Vuex);

const getCookie = name => {
  const cookies = `; ${document.cookie}`.match(`;\\s*${name}=([^;]+)`);
  return cookies ? cookies[1] : ''
};

export default new Vuex.Store({
  name: 'state',
  plugins: [store => {
    store.subscribe((mutation, state) => {
      switch (mutation.type) {
        case 'SET_USER_INFO':
          sessionStorage.setItem('userInfo', JSON.stringify(state.userInfo));
          break;
        case 'SET_DASHBOARD_LESSONS':
          break;
        case 'SET_CURRENT_LESSON':
          sessionStorage.setItem('currentLesson', JSON.stringify(state.currentLesson));
          break;
        case 'SET_MAIN_COURSE':
          sessionStorage.setItem('mainCourse', JSON.stringify(state.mainCourse));
          break;
      }
    })
  }],

  state: {
    isUserAuthenticated: !!(getCookie('email')),
    userInfo: {},
    mainCourse:  {},
    currentLesson: {},
    nextLessons: false
  },
  mutations: {
    SET_IS_USER_AUTHENTICATED (state, isUserAuthenticated) {
      state.isUserAuthenticated = isUserAuthenticated
    },
    SET_USER_INFO (state, userInfo) {
      state.userInfo = userInfo
    },
    SET_USER_LEVEL (state, level) {
      state.userInfo.level = level
    },
    SET_USER_CHARSET (state, charSet) {
      state.userInfo.charSet = charSet
    },
    SET_CURRENT_LESSON (state, lessonData) {
      state.currentLesson = lessonData
    },
    TOGGLE_CURRENT_LESSON_STUDIED (state, studied) {
      state.currentLesson.studied = studied
    },
    TOGGLE_CURRENT_LESSON_SAVED (state, saved) {
      state.currentLesson.saved = saved
    },
    SET_MAIN_COURSE (state, mainCourse) {
      state.mainCourse = mainCourse
    }
  },
  actions: {
    getUserInfo (context) {
      return new Promise((resolve, reject) => {
        axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/get-stats')
          .then(({data}) => {
            context.commit('SET_USER_INFO', data);
            resolve()
          })
          .catch(error => reject(error));
      })
    },
    changeLevel (context, level) {
      return new Promise((resolve, reject) => {
        context.commit('SET_USER_LEVEL', level);
        axios.put(process.env.VUE_APP_ROOT_API + '/api/v1/onboarding/level', {level: level})
          .then(() => {
            context.dispatch('getUserInfo');
            resolve()
          })
          .catch(error => reject(error));
      })
    },
    changeCharSet (context, charSet) {
      return new Promise((resolve, reject) => {
        context.commit('SET_USER_CHARSET', charSet);
        axios.put(process.env.VUE_APP_ROOT_API + '/api/v1/onboarding/level', {charSet: charSet})
          .then(() => {
            // context.dispatch('getUserInfo');
            resolve()
          })
          .catch(error => reject(error));
      })
    },
    currentLesson (context, lessonData) {
      return new Promise((resolve, reject) => {
        context.commit('SET_CURRENT_LESSON', lessonData);
        resolve();
      })
    },
    toggleCurrentLessonStudied (context, payload) {
      return new Promise((resolve, reject) => {
        context.commit('TOGGLE_CURRENT_LESSON_STUDIED', payload.studied);
        axios.post(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/toggle-studied', {
          lessonId: payload.lessonId,
          status: payload.studied
        }).then(() => {resolve()})
          .catch(() => {reject()});
      })
    },
    toggleCurrentLessonSaved (context, payload) {
      return new Promise((resolve, reject) => {
        context.commit('TOGGLE_CURRENT_LESSON_SAVED', payload.saved);
        axios.post(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/toggle-saved', {
          lessonId: payload.lessonId,
          status: payload.saved
        }).then(() => {resolve()})
          .catch(() => {reject()});
      })
    },
    getMainCourse (context, courseId) {
      return new Promise((resolve, reject) => {
        axios.get(process.env.VUE_APP_ROOT_API + `/api/v1/dashboard/course-lessons?courseId=${courseId}`)
          .then(({data}) => {
            context.commit('SET_MAIN_COURSE', data);
            resolve()
          })
          .catch(error => reject(error));
      })
    }
  }
})
